import classNames from "classnames";
import { useRef } from "react";
import Draggable from "react-draggable";
import styles from "./microsoft-edge.module.scss";
function Control({ name, hoverStyle, onClick = function () {} }) {
  return (
    <div className={classNames(styles.control, hoverStyle)} onClick={onClick}>
      <i className={name}></i>
    </div>
  );
}
function MicrosoftEdge({ open = false, onClose = function () {} }) {
  const appRef = useRef(null);
  const onDrag = (event) => {
    // const { clientY } = event;
  };
  if (!open) return null;
  return (
    <Draggable nodeRef={appRef} handle="#appBar" onDrag={onDrag}>
      <div className={styles.microsoftEdge} ref={appRef}>
        <div className={styles.appBar} id="appBar">
          <div className={styles.tab}></div>
          <div className={styles.appControl}>
            {/* <i class="fa-solid fa-expand"></i> */}
            <Control name="fa-solid fa-minus" />
            <Control name="fa-solid fa-down-left-and-up-right-to-center" />
            <Control
              name="fa-solid fa-xmark"
              onClick={onClose}
              hoverStyle={styles.closeButtonHover}
            />
          </div>
        </div>
        <div className={styles.body}></div>
      </div>
    </Draggable>
  );
}

export { MicrosoftEdge };
