import { useEffect, useRef, useState } from "react";
import { MicrosoftEdge } from "../../Components/Apps/MicrosoftEdge";
import { Tool } from "../../Components/Tool/Tool";
import { Toobar } from "../../Components/Toolbar/Toolbar";
import { handleClickOutside, handleRightClick } from "../../utils";
import styles from "./os.module.scss";

function OS() {
  const [showTool, setShowTool] = useState(false);
  const [offset, setOffset] = useState({ offsetX: 0, offsetY: 0 });
  const toolRef = useRef(null);

  const handleTool = (e) => {
    handleRightClick(e, (offset) => {
      setShowTool(true);
      setOffset(offset);
    });
  };
  const handleOutside = (e) =>
    handleClickOutside(e, setShowTool, toolRef, false);
  useEffect(() => {
    document.addEventListener("contextmenu", handleTool);
    document.addEventListener("mousedown", handleOutside);
    return () => {
      document.removeEventListener("contextmenu", handleTool);
      document.removeEventListener("mousedown", handleOutside);
    };
  });
  const [openMicrsoftEdge, setOpenMicrosoftEdg] = useState(false);
  const handleAppRunning = (e) => {
    if (e.name === "Microsoft Edge") {
      setOpenMicrosoftEdg(true);
    }
  };
  return (
    <div className={styles.os}>
      <div ref={toolRef} style={{ background: "red" }}>
        {showTool && <Tool offset={offset} />}
      </div>
      <MicrosoftEdge
        open={openMicrsoftEdge}
        onClose={() => setOpenMicrosoftEdg(false)}
      />
      <Toobar onAppRunning={handleAppRunning} />
    </div>
  );
}

export { OS };
