import { useEffect, useRef } from "react";
import styles from "./tool.module.scss";
function Tool({ offset }) {
  const { offsetX, offsetY } = offset;
  const toolRef = useRef(null);
  const moveTool = (x, y) => {
    if (toolRef.current) {
      const { style } = toolRef.current;
      style.top = `${y}px`;
      style.left = `${x}px`;
    }
  };
  useEffect(() => {
    moveTool(offsetX, offsetY);
  }, [offsetX, offsetY]);
  return (
    <div className={styles.tool} ref={toolRef} id="tools">
      Tool
    </div>
  );
}
export { Tool };
