export const handleClickOutside = (event, callback, wrapperRef, value) => {
  const { current: wrap } = wrapperRef;
  if (wrap && !wrap.contains(event.target)) {
    callback(value);
  }
};

export const handleRightClick = (e, callback) => {
  e.preventDefault();
  const NO_ACTIONS = ["tools"];
  if (e.target.id && NO_ACTIONS.includes(e.target.id)) {
    return null;
  }
  const { offsetX, offsetY } = e;
  callback({ offsetX, offsetY });
};
