import classNames from "classnames";
import { useEffect, useRef, useState } from "react";
import { handleClickOutside } from "../../utils";
import { WindowMenu } from "../WindowMenu/WindowMenu";
import styles from "./toolbar.module.scss";

function Icon({ name, hoverable = false, onClick = function () {} }) {
  return (
    <span
      className={classNames(styles.icon, { [styles.hover]: hoverable })}
      onClick={onClick}
    >
      <i className={name}></i>
    </span>
  );
}

function Time() {
  const [date, setDate] = useState("");
  const [time, setTime] = useState("");
  useEffect(() => {
    const interval = setInterval(() => {
      const datetime = new Date();
      setDate(
        `${datetime.getDate()}/${datetime.getMonth()}/${datetime.getFullYear()}`
      );
      setTime(`${datetime.getHours()}:${datetime.getMinutes()}`);
    }, 1000);
    return () => clearInterval(interval);
  }, []);
  return (
    <div className={`${styles.time} ${styles.hover}`}>
      <div>{time}</div>
      <div>{date}</div>
    </div>
  );
}
function Toobar({onAppRunning}) {
  const [showMenu, setShowMenu] = useState(false);
  const toolbarRef = useRef(null);
  const handleControl = (event) => {
    if (toolbarRef.current) {
      handleClickOutside(event, setShowMenu, toolbarRef, false);
    }
  };
  useEffect(() => {
    window.addEventListener("mousedown", handleControl);
    return () => {
      window.removeEventListener("mousedown", handleControl);
    };
  });
  const toggleMenu = () => {
    setShowMenu((show) => !show);
  };
  return (
    <div className={styles.toolbar} ref={toolbarRef}>
      <WindowMenu status={showMenu} handleApp={onAppRunning} />
      <span className={styles.container}>
        <div className={styles.leftIcons}>
          <Icon name="fa-brands fa-windows" hoverable onClick={toggleMenu} />
        </div>
        <div className={styles.rightIcons}>
          <div className={styles.miniIcon}>
            <span className={styles.icon}>
              <Icon name="fa-solid fa-wifi" />
            </span>
            <span className={styles.icon}>
              <Icon name="fa-solid fa-volume-high" />
            </span>
          </div>
          <Time />
          <span className={styles.icon}>
            <Icon name="fa-regular fa-message" />
          </span>
        </div>
      </span>
    </div>
  );
}
export { Toobar };
