import classNames from "classnames";
import "./Loader.scss";
function Loader({ status }) {
  return (
    <div
      className={classNames([
        "loading-container",
        status ? "loading" : "loading-done",
      ])}
    >
      <div className="avatar-wrapper">
        <img
          src={require("../../assets/images/avatar.png")}
          alt="avatar"
          className="loading-avatar"
        />
        <div className="loading-text">
          <span>L</span>
          <span>O</span>
          <span>A</span>
          <span>D</span>
          <span>I</span>
          <span>N</span>
          <span>G</span>
          <span>.</span>
          <span>.</span>
          <span>.</span>
        </div>
      </div>
    </div>
  );
}

export { Loader };
