import classNames from "classnames";
import { useEffect, useRef, useState } from "react";
import { handleClickOutside } from "../../utils";
import styles from "./window-menu.module.scss";

function Icon({ name }) {
  return <i className={name}></i>;
}
function Control({ toggleControl = function () {}, icon = "", label = "" }) {
  return (
    <div className={styles.control} onClick={toggleControl}>
      <span className={styles.controlIcon}>
        <Icon name={icon} />
      </span>
      <span>{label}</span>
    </div>
  );
}
function ControlGroup() {
  const controlRef = useRef(null);
  const [showControl, setShowControl] = useState(false);
  const toggleControl = () => setShowControl((show) => !show);
  const handleControl = (event) => {
    if (controlRef.current) {
      handleClickOutside(event, setShowControl, controlRef, false);
    }
  };
  useEffect(() => {
    window.addEventListener("mousedown", handleControl);
    return () => {
      window.removeEventListener("mousedown", handleControl);
    };
  });
  return (
    <div
      ref={controlRef}
      className={classNames(styles.controlGroup, {
        [styles.show]: showControl,
      })}
    >
      <Control
        toggleControl={toggleControl}
        icon="fa-solid fa-bars"
        label="START"
      />
      <div>
        <Control icon="fa-regular fa-image" label="Pictures" />
        <Control icon="fa-solid fa-gear" label="Setting" />
        <Control icon="fa-solid fa-power-off" label="Shutdown" />
      </div>
    </div>
  );
}

function AppItem({ label, icon, onOpen = function () {} }) {
  const handleAppOpen = () => {
    onOpen({ name: label });
  };
  return (
    <div className={styles.item} onClick={handleAppOpen}>
      <div className={styles.icon}>
        {icon && <img src={icon} alt="app-item-icon" />}
      </div>
      <span className={styles.label}>{label}</span>
    </div>
  );
}
function AppList({ handleApp = function () {} }) {
  return (
    <div className={styles.appList}>
      <div className={styles.appGroup}>
        <div className={styles.title}>Recently added</div>
        <div className={styles.body}>
          <AppItem
            onOpen={handleApp}
            label="FireFox"
            icon={require("../../assets/images/logo-firefox-browser.fbc7ffbb50fd.png")}
          />
          <AppItem
            onOpen={handleApp}
            label="Visual Code"
            icon={require("../../assets/images/file_type_vscode_icon_130084.png")}
          />
        </div>
      </div>
      <div className={styles.appGroup}>
        <div className={styles.title}>A</div>
        <div className={styles.body}>
          <AppItem label="Access" />
          <AppItem label="Adobe Photoshop 2023" />
          <AppItem label="AnyDesk" />
        </div>
      </div>
      <div className={styles.appGroup}>
        <div className={styles.title}>B</div>
        <div className={styles.body}>
          <AppItem label="BlueStack X" />
        </div>
      </div>
    </div>
  );
}

function AppGrid({ handleApp = function () {} }) {
  return (
    <div className={styles.appGrid}>
      <div className={styles.appGroup}>
        <div className={styles.title}>Productivity</div>
        <div className={styles.body}>
          <AppItem
            onOpen={handleApp}
            label="Office"
            icon={require("../../assets/images/Microsoft_Office_logo_(2013–2019).png")}
          />
          <AppItem
            onOpen={handleApp}
            label="Microsoft Edge"
            icon={require("../../assets/images/Microsoft_Edge_logo_(2019).png")}
          />
          <AppItem
            label="Instagram"
            icon={require("../../assets/images/Instagram-Icon.png")}
          />
        </div>
      </div>
      <div className={styles.appGroup}>
        <div className={styles.title}>Explore</div>
        <div className={styles.body}>
          <AppItem label="Microsoft Store" />
          <AppItem label="Paint 3D" />
        </div>
      </div>
    </div>
  );
}
function WindowMenu({ status = false, handleApp }) {
  return (
    <div className={classNames(styles.windowMenu, { [styles.show]: status })}>
      <ControlGroup />
      <AppList handleApp={handleApp} />
      <AppGrid handleApp={handleApp} />
    </div>
  );
}

export { WindowMenu };
